.container{
    width: 80%;
    height: 664px;
    margin: 20px auto;
    background-color: #3B38F1;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.item1{
    margin: 16px;
    padding: 40px;
    width: 40%;
    height: 90%;
    position: relative;
}
.item1 h2{
    color: white;
    font-family: apercu;
    font-weight: 900;
    font-size: 36px;
}
.item1 p{
    color: white;
    margin-top: 40px;
    font-family: apercu;
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
    text-align: justify;
    margin-right: 40px;
}
.item1 img{
    width: 250px;
    height: 250px;
    text-align: center;
    margin: auto;
    position: absolute;
    bottom: 15%;
    left: 15%;
}

.item2{
    width: 60%;
    margin: 16px;
    /* background: #9492ff4b; */
    height: 90%;
    padding: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.label{
    width: 100%;
    color: white;
    font-size: 16px;
    font-family: apercu;
    font-weight: 700;
}

.form{
    width: 100%;
    margin-right: 40px;
}

.error_msg{
    color: rgb(250, 99, 99);
    size: 14px;
    text-align: end;
    font-family: apercu;
    font-weight: 500;
}

.form_field{
    background: #3B38F1;
    width: 100%;
    height: 300px;
    border: none;
    margin: 10px 0;
    color: white;
    padding: 10px;
    font-size: 20px;
    font-family: apercu;
    font-weight: 700;
    
}
.phoneclass{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.form_group1{
    flex: 2;
    margin-right: 10px;
}

.form_group2{
    flex: 1;
    margin-left: 10px;
}
.btn{
    width: 100%;
    height: 62px;
    background: white;
    border-radius: 31px;
    font-size: 20px;
    border: none;
    margin: 20px 0;
    font-family: apercu;
    font-weight: 500;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(219, 224, 250);
    opacity: 1; /* Firefox */
  }

@media screen and (max-width: 1300px) {
    .item1 h2{
        font-size: 32px;
    }
    .item1 p{
        font-size: 20px;
    }
    .item1 img{
        bottom: 10%;
        left: 15%;
    }
}
@media screen and (max-width: 1200px) {
    .item1 p{
        font-size: 16px;
    }
}
@media screen and (max-width: 1000px) {
    .container{
        width: 90%;
    }
    .item1{
        padding: 40px 0 40px 40px;
    }
    .item1 img{
        bottom: 5%;
        left: 15%;
    }
}

@media screen and (max-width: 800px) {
    .container{
        width: 100%;
        margin: 0;
        border-radius: 0;
        flex-direction: column;
        height: auto;
    }
    .item1{
        width: 100%;
        padding: 20px;
        position: relative;
    }
    .item1 img{
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        text-align: center;
        top: -100px;

    }
    .item1 h2{
        font-size: 36px;
        margin-top: 200px;
        text-align: center;
    }
    .item1 p{
        font-size: 24px;
        margin: auto;
        margin-top: 10px;
        text-align: center;
    }
    .item2{
        width: 100%;
        margin: 10px ;
        padding: 20px;
    }
    .form_field{
        background: white;
        width: 100%;
        height: 54px;
        border: none;
        margin: 10px 0;
        padding: 40px;
        color: #3B38F1;
        padding: 10px;
        font-size: 20px;
    }
    .form{
        margin-right: 0;
        padding:0 ;
    }
}

